<template>
  <div class="flex sm:flex-row  flex-col items-center  lg:mt-12 mt-5 justify-center transition-all">
      <div v-for="metric in metrics" :key="metric.title" class="flex flex-col rounded-xl p-3 border border-gray-100 shadow-md w-36 lg:mr-5 xl:mr-20 mr-4 mb-6 transition-all duration-200 min-w-max">
          <p class="lg:text-2xl text-lg font-bold">{{metric.title}}</p>
          <p class="lg:text-xl font-semibold text-gray-600">{{metric.value}}</p>
      </div>
  </div>
</template>

<script>
export default {
    name:"DashBoard",
    data() {
    return {
      metrics: [
        { value: '3', title: "Machines" },
        { value: "12", title: "Modeles" },
        { value: "12", title: "Libre" },
        { value: "9", title: "En pane" },
      ],
    };
  },
  watch: {
    machineCount() {
      let count = this.metrics.find(metrics => metrics.title === "Machines");
      count.title = this.$store.state.getMachineCount.results
       return this.$store.state.getMachineCount.results
    }
  }
}
</script>

