<template>
  <div class="flex justify-center table-auto">
      <table :class="border">
          <th v-for="header in headers" :key="header.title" :class="header.class+' '+border"  class="lg:p-7 p-1 text-center xl:text-lg md:texl-sm text-sm font-semibold">{{header.title}}</th>
          <tr v-for="el in machines" :key="el.model_id">
              <td :class="tableRowBg(el)" class="xl:p-2 lg:p-1 md:p-1">
                  {{el.name}}
              </td>
              <td  :class="border" class="sm:table-cell hidden">
                  {{el.serial_number}}
              </td>
              <td :class="border" class="sm:table-cell hidden">
                  {{el.reference}}
              </td>
              <td :class="border">
                  {{el.model.name}}
              </td>
              <td :class="border">
                  {{el.model.ram}} Go
              </td>
              <td :class="border">
                  {{el.storage}} Go
              </td>
              <td :class="border" >
                  {{el.model.cpu}}
              </td>
              <td :class="border" class="lg:p-2 xl:table-cell hidden">
                  {{el.os.name}} {{el.os.type}}
              </td>
          </tr>
      </table>
  </div>
</template>

<script>
export default {
    name:"DataTable",
    data(){
        return{
            border:"border border-gray-500 p-1",
            headers:[
                {title:"Machine",class:""},
                {title:"SN",class:"sm:table-cell hidden"},
                {title:"Reference",class:"sm:table-cell hidden"},
                {title:"Model",class:""},
                {title:"RAM",class:""},
                {title:"Stockage",class:""},
                {title:"Processeur",class:""},
                {title:"OS",class:"xl:table-cell hidden"},
            ],
           
        }
    },
    methods:{
        tableRowBg(el){
           
            if(el.assigned){
               return this.border + " " + "bg-red-100"
            }else{
               return this.border + " " + "bg-green-100"
            }
        }
    },
    mounted () {
    this.$store.dispatch("getMachines");
  },
  computed:{
      machines(){
          return this.$store.state.machines.results
      }
  }
}
</script>

