import Vue from 'vue'
import Vuex from 'vuex'
let server = "http://127.0.0.1:8000/";
Vue.use(Vuex)


export default new Vuex.Store({
  state: {
    machines: [],
    machineCount: "",
    modelsCount: "",
    aviable: "",
  },
  mutations: {
    setMachine(state, data) {
      state.machines = data;
    },
  },
  actions: {
    async getMachines(context) {
      const { data } = await axios({
        method: "get",
        url: server + "api/inventory/machine/",
      });
      context.commit("setMachine", data);
    },

    async searchMachines(context, q) {
      const { data } = await axios({
        method: "post",
        url: server + "api/inventory/machine/search/",
        data: {
          query: q,
        },
      }).catch(function(error) {
        if (error.response.status == 404) {
          context.commit("setMachine", []);
        }
      });
      context.commit("setMachine", data);
    },

    async filterMachines(
      context,
      { machine, model, ref, sn, cpu, os, ram, stockage }
    ) {
      print("test")
      const { data } = await axios({
        method: "post",
        url: server + "api/inventory/machine/filter/",
        data: {
          name__icontains: machine,
          model__name__icontains: model,
          reference: ref,
          serial_number: sn,
          model__cpu__icontains: cpu,
          os__name__icontains: os,
          model__ram: ram,
          stockage:stockage
        },
      }).catch(function(error) {
        if (error.response.status == 404) {
          context.commit("setMachine", []);
        }
      });
      context.commit("setMachine", data);
    },
    async getMachineCount(context) {
      const { data } = await axios({
        method: "get",
        url: server + "api/inventory/machine/",
      });
      context.commit("setMachine", data.count);
    },
  },
  modules: {},
});
